import React from 'react';
import Helmet from 'react-helmet';
import L from 'leaflet';
import axios from 'axios';
import Layout from 'components/Layout';
import Map from 'components/Map';

const LOCATION = {
  lat: 0,
  lng: 0
};
const CENTER = [LOCATION.lat, LOCATION.lng];
const DEFAULT_ZOOM = 2;

const IndexPage = () => {
  /**
   * mapEffect
   * @description Fires a callback once the page renders
   * @example Here this is and example of being used to zoom in and set a popup on load
   */

  async function mapEffect({ leafletElement: map } = {}) {
    //mod start
  const { data: countries = [] } = await axios.get('https://corona.lmao.ninja/v2/countries');
  const { data: stats = {} } =await axios.get('https://corona.lmao.ninja/v2/all');
  const dashboardStats = [
    {
      primary: {
        label: 'Total Cases',
        value: stats?.cases
      },
      secondary: {
        label: 'Per 1 Million',
        value: stats?.casesPerOneMillion
      }
    },
    {
      primary: {
        label: 'Total Deaths',
        value: stats?.deaths
      },
      secondary: {
        label: 'Per 1 Million',
        value: stats?.deathsPerOneMillion
      }
    },
    {
      primary: {
        label: 'Total Tests',
        value: stats?.tests
      },
      secondary: {
        label: 'Per 1 Million',
        value: stats?.testsPerOneMillion
      }
    }
  ]
  console.log(dashboardStats);
  //mod end
    /*let response;

    try {
      response = await axios.get('https://corona.lmao.ninja/v2/countries');
    } catch(e) {
      console.log(`Failed to fetch countries: ${e.message}`, e);
      return;
    }

    const { data = [] } = response;
    const hasData = Array.isArray(data) && data.length > 0;

    if ( !hasData ) return;*/

    const geoJson = {
      type: 'FeatureCollection',
      //features: data.map((country = {}) => {
        features: countries.map((country = {}) => {
          //above line is v2
        const { countryInfo = {} } = country;
        const { lat, long: lng } = countryInfo;
        return {
          type: 'Feature',
          properties: {
            ...country,
          },
          geometry: {
            type: 'Point',
            coordinates: [ lng, lat ]
          }
        }
      })
    }

    const geoJsonLayers = new L.GeoJSON(geoJson, {
      pointToLayer: (feature = {}, latlng) => {
        const { properties = {} } = feature;
        let updatedFormatted;
        let casesString;

        const {
          country,
          updated,
          cases,
          deaths,
          recovered
        } = properties

        casesString = `${cases}`;

        if ( cases > 1000 ) {
          casesString = `${casesString.slice(0, -3)}k+`
        }

        if ( updated ) {
          updatedFormatted = new Date(updated).toLocaleString();
        }

        const html = `
          <span class="icon-marker">
            ${ casesString }
          </span>
        `;
        const popupHtml = `
          <span class="icon-marker-tooltip">
            <h2>${country}</h2>
            <ul>
              <li><strong>Confirmed:</strong> ${cases}</li>
              <li><strong>Deaths:</strong> ${deaths}</li>
              <li><strong>Recovered:</strong> ${recovered}</li>
              <li><strong>Last Update:</strong> ${updatedFormatted}</li>
            </ul>
          </span>
        `;

        const popup = L.popup({
          maxWidth: 400
        }).setContent(popupHtml);

        return L.marker( latlng, {
          icon: L.divIcon({
            className: 'icon',
            html
          }),
          riseOnHover: true
        }).bindPopup(popup);
      }
    });

    geoJsonLayers.addTo(map)
    //v2
    document.getElementById('label1').innerHTML=dashboardStats[0].primary.label+': '+dashboardStats[0].primary.value;
    document.getElementById('label2').innerHTML=dashboardStats[1].primary.label+': '+dashboardStats[1].primary.value;
    document.getElementById('label1b').innerHTML=dashboardStats[0].secondary.label+': '+dashboardStats[0].secondary.value;
    document.getElementById('label2b').innerHTML=dashboardStats[0].secondary.label+': '+dashboardStats[0].secondary.value;  
  }

  const mapSettings = {
    center: CENTER,
    defaultBaseMap: 'Mapbox',
    zoom: DEFAULT_ZOOM,
    mapEffect
  };

  return (
    <Layout pageName="home">
      <Helmet>
        <title>COVID-19 TRACKER</title>
      </Helmet>
      <Map {...mapSettings} />
      <div id="test1" style={{float:'left',backgroundColor:'red'}}>
      <strong><p id="label1"/></strong>
      <strong><p id="label1b"/></strong>
      </div>
      <div id="test2" style={{float:'right',backgroundColor:'red'}}>
      <strong><p id="label2"/></strong>
      <strong><p id="label2b"/></strong>
      </div>
    </Layout>
  );
};

export default IndexPage;

